}

.tab{
  /* margin: 20px; */
  font-weight:300;
  font-size: 16px;
  line-height: 2;
}

.title {
  font-size: 25px;
  color: darkgrey;
  line-height: 1;
  padding-bottom: 10px;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.title:hover {
  text-decoration: none;

}

.detail {
  padding: 20px;
  height: inherit;
  overflow-y: scroll;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.3)), to(transparent));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.3), transparent);
  background: -o-linear-gradient(rgba(255, 255, 255, 0.3), transparent);
  background: linear-gradient(rgba(255, 255, 255, 0.3), transparent);
  /* background-color: rgba(255, 255, 255, 0.3); */
  /* border-radius: 0px 0px 20px 20px; */
}

